.Skilles{
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    height: 80%;
    width: 60%;
}
.logo{
    width: 40px;
    height: 40px;
    border-radius: 50%;
    
    
}
.item{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    height: 50px;
    
}
.full-progress{
    width: 200px;
    background-color: white;
}
.full-progress,.progress{
    height: 5px;
    border-radius: 20px;
}
.progress{
    
    background: linear-gradient(
        90deg,
        #da22ff,
        #9733ee
    );
    
}