body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: white;
  overflow: hidden;
  
}
*{
  /*transition: all 1s linear;*/
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
/*  main  */
.main{
  width: 100%;
  background-color: #002951;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}
.nav{
  width: 5%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
  height: 60%;
  background-color: rgba(43, 226, 171, 0);
  color: rgb(255, 255, 255);
  z-index: 1;
  width: 20%;
}
.link{
  text-decoration: none;
  color: white;
  margin-right: 20px;
  display: flex;
  align-items: center;
}
.select{
  width: 10px;
  height: 60%;
  border-radius: 0 20px 20px 0;
  background-color: #002951;
  
}
.bal{
  width: 8px;
  height: 8px;
  border-radius: 30%;
  border: 1px solid rgba(165, 188, 236, 0.336);
  background-color: #ffffff;
  position: absolute;
  left: -20px;
  transition: all .3s linear;
}
.select,.white{
  position: absolute;
  
}
.white{
  height: 100%;
  width: 80px;
  background-color: white;
  background: linear-gradient(to right,rgba(165, 188, 236, 0.336), #002951);
}
/*  main  */
/*  tab start  */
.tabs{
  width: 50%;
  height: 100%;
  background-color: rgba(255, 0, 0, 0);
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
}
.name{
  position: absolute;
  top: 0;
  left: 0;
  transform: translateY(-40px);
  /*transition: transform 1s cubic-bezier(0.23, 1, 0.320, 1);*/
  transition: transform 1s cubic-bezier(0.68, -0.55, 0.265, 1.55);
}
.Exp{
  height: 50%;
  background-color: rgba(0, 0, 255, 0);
  display: flex;
  align-items: center;
  justify-content: center;
}
.Exp ul{
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 70%;
  background-color: rgba(0, 0, 0, 0);
}
/*  tab end  */
/*  img start  */
.photo{
  position: relative;
}
.img{
  background-color: rgba(220, 243, 12, 0);
  width: 50%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.img .photo{
  width: 470px;
  height: 600px;
  
}
.photo img{
  position: absolute;
  z-index: 1;
}
.blur-img{
  position: absolute;
  width: 100%;
  height: 100%;
  background: linear-gradient(rgba(255, 0, 0, 0) , rgba(255, 0, 0, 0) ,rgb(0 41 81));
  z-index: 2;
}
/*  img end  */
/*  c  */
.corner{
  background-color: #00172D;
  width: 70px;
  height: 170px;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  border-top-left-radius: 20px;
  position: absolute;
  bottom: 0;
  right: 0;
  z-index: 3;
}
.corner>*{
  width: 20px;
  height: 20px;
  color: rgb(228, 228, 228);
  
}
.hr{
  width: 90%;
}
/*  c  */
.bold{
  font-weight: bold;
}

